.colmarAcademy html{
    font-family: Helvetica;
    font-size: 1vw
}

.colmarAcademy {
    background-color: #add8e6
}

@media screen and (min-width:400px) {
    .colmarAcademy {
        background-color: #90ee90
    }
}

@media screen and (min-width:800px) {
    .colmarAcademy {
        background-color: #e6e6fa
    }
}

@media (min-width:481px) {
    .colmarAcademy {
        max-width: 80%;
        margin-left: auto;
        margin-right: auto;
        min-width: 50%
    }
}

@media (max-width:480px) {
    .colmarAcademy {
        max-width: 90%;
        margin-left: 5%;
        margin-right: 5%
    }
}

@media (min-width:481px) {
    .colmarAcademy header {
        min-height: 32px;
        display: -webkit-inline-box;
        display: -ms-inline-flexbox;
        display: inline-flex;
        width: 100%;
        -webkit-box-pack: justify;
        -ms-flex-pack: justify;
        justify-content: space-between
    }
}

@media (max-width:480px) {
    .colmarAcademy .desktop {
        display: none
    }
    .colmarAcademy .mobile {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex
    }
}

@media (min-width:481px) {
    .colmarAcademy .desktop {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex
    }
    .colmarAcademy .mobile {
        display: none
    }
}

.colmarAcademy .logo {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding-left: 24px;
    font-weight: 600
}

.colmarAcademy .logo:hover {
    cursor: pointer
}

.colmarAcademy .grey {
    color: grey
}

.colmarAcademy .lightgray {
    color: #d3d3d3
}

.colmarAcademy nav {
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    padding-right: 16px
}

.colmarAcademy ul {
    list-style-type: none;
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex
}

.colmarAcademy li {
    padding: 6px
}

.colmarAcademy li:hover {
    cursor: pointer;
    text-decoration: underline
}

.colmarAcademy .banner {
    background-color: #d3d3d3;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center
}

.colmarAcademy .banner img {
    width: 100%;
    -o-object-fit: contain;
    object-fit: contain
}

.colmarAcademy .banner h1 {
    font-size: 2.5vw;
    font-weight: 700
}

.colmarAcademy .banner p {
    font-size: 1.5vw;
    font-weight: 700
}

.colmarAcademy .banner button {
    background-color: #000;
    color: #fff;
    padding-top: 8px;
    padding-bottom: 8px;
    width: 80%;
    border: 0;
    font-size: 1.5vw
}

.colmarAcademy button:hover {
    cursor: pointer
}

.colmarAcademy .banner-img {
    width: 60%;
    padding: 32px 24px
}

.colmarAcademy .banner-text {
    padding: 32px 24px;
    width: 40%;
    height: 100%
}

.colmarAcademy .information {
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex
}

.colmarAcademy .information img {
    width: 100%;
    -o-object-fit: contain;
    object-fit: contain
}

.colmarAcademy .main {
    max-width: 55%;
    padding-top: 32px;
    padding-left: 24px;
    padding-right: 0;
    margin-bottom: 32px
}

.colmarAcademy .main:hover {
    cursor: pointer
}

.colmarAcademy .main h1 {
    font-size: 2vw;
    padding: 24px 0 12px
}

.colmarAcademy .main h2 {
    font-size: 1.25vw;
    font-weight: 500;
    padding-top: 12px
}

.colmarAcademy .main p {
    padding-top: 0;
    font-size: 1.25vw
}

.colmarAcademy .others {
    width: 45%;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    padding: 16px 12px
}

.colmarAcademy .other {
    width: 100%;
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    border-left: thin solid #d3d3d3
}

.colmarAcademy .other:hover {
    background-color: #d3d3d3;
    cursor: pointer
}

.colmarAcademy .other-image {
    width: 35%;
    padding: 16px
}

.colmarAcademy .other-image img {
    max-width: 100%;
    -o-object-fit: cover;
    object-fit: cover
}

.colmarAcademy .other-text {
    width: 65%
}

.colmarAcademy .other h1 {
    font-size: 1.5vw;
    margin-left: 8px
}

.colmarAcademy .other p {
    font-size: 1vw;
    margin: 8px
}

.colmarAcademy #read-more {
    color: #0056ba;
    font-size: 1vw;
    margin-left: 8px
}

.colmarAcademy .courses {
    width: 90%;
    background-color: #d3d3d3;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    padding: 5%
}

.colmarAcademy .title {
    margin-top: 12px;
    margin-bottom: 32px;
    text-align: center;
    font-size: 1.5vw
}

.colmarAcademy .content {
    margin-bottom: 10px;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center
}

.colmarAcademy .content,
.colmarAcademy .course {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex
}

.colmarAcademy .course {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    margin: 8px;
    background-color: #fff;
    width: 30%;
    padding-bottom: 8px
}

.colmarAcademy .course:hover {
    cursor: pointer
}

.colmarAcademy .course img {
    width: 100%
}

.colmarAcademy .course h1 {
    padding-top: 16px;
    padding-left: 16px;
    padding-bottom: 8px
}

.colmarAcademy .course p {
    padding-left: 16px;
    margin-bottom: 8px;
    margin-top: 0
}

.colmarAcademy .thesis,
.colmarAcademy .thesis video {
    width: 100%
}

.colmarAcademy .thesis-content {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex
}

.colmarAcademy .thesis-main {
    width: 60%;
    margin-left: 24px;
    margin-right: 24px
}

.colmarAcademy .thesis-others {
    width: 40%
}

.colmarAcademy .thesis-other,
.colmarAcademy footer {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex
}

.colmarAcademy footer {
    width: 100%;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    border-top: thin solid #d3d3d3
}

.colmarAcademy h1 {
    font-weight: 700;
    margin: 0
}

.colmarAcademy p {
    color: #555
}

.colmarAcademy .row-header {
    margin: 0 auto;
    padding: 0
}

.colmarAcademy .row-content {
    margin: 0 auto;
    padding: 50px 0;
    border-bottom: 1px ridge;
    min-height: 400px
}

.colmarAcademy .footer {
    background-color: #d1c4e9;
    margin: 0 auto;
    padding: 20px 0
}

.colmarAcademy .jumbotron {
    margin: 0 auto;
    padding: 70px 30px;
    background: #9575cd;
    color: #fffaf0
}

.colmarAcademy address {
    font-size: 80%;
    margin: 0;
    color: #0f0f0f
}

.colmarAcademy body {
    padding: 50px 0 0;
    z-index: 0
}

.colmarAcademy .navbar-dark {
    background-color: #512da8
}

.colmarAcademy .tab-content {
    border-left: 1px solid #ddd;
    border-right: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    padding: 10px
}

.colmarAcademy .carousel {
    background: #512da8
}

.colmarAcademy .carousel .carousel-item {
    height: 300px
}

.colmarAcademy .carousel .carousel-item img {
    position: absolute;
    top: 0;
    left: 0;
    min-height: 300px
}

.colmarAcademy #carouselButton {
    position: absolute;
    right: 0;
    bottom: 0
}

.colmarAcademy .modal .modal-header {
    background-color: #512da8;
    color: #fffaf0
}

.colmarAcademy .modal .modal-header button {
    color: #fffaf0
}

.colmarAcademy .modal .modal-body {
    background-color: #d1c4e9
}

.colmarAcademy p {
    font-size: calc(12px + 9*(100vw - 420px)/860)
}

@media screen and (min-width:1280px) {
    .colmarAcademy p {
        font-size: 21px
    }
}

@media screen and (max-width:420px) {
    .colmarAcademy p {
        font-size: 12px
    }
}

