.excursion {
  font-family: verdana;
  background-color: black;
  color: white;
  text-align: center;
  margin-bottom: 0;
}

.excursion h1 {
  font-size: 50px;
  font-weight: normal;
}

.excursion h2 {
  font-size: 42px;
  font-weight: 300;
}

.excursion h3 {
  font-size: 21px;
  color: gray;
}

.excursion h4 {
  font-size: 16px;
  color: aquamarine;
  text-decoration: underline;

}

.excursion p {
  text-align: right;
  color: gray;
  margin:0;
}

.excursion video {
  position: static;
}

.excursion img {
  position: static;
  padding: 5px;
  margin: 5px;
}

.excursion a:active{
  color: yellow;
}
