/* Universal Styles */

.ravenous {
  font-size: 18px;
  background-color: #9e6434;
  margin-top: 6vh;
}

@media only screen and (max-width: 560px) {
  .ravenous {
    font-size: 15px;
  }
}

.ravenous input:focus {
  outline: none;
}

/* Reusable Component - Image Container */

.ravenous .image-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
  background: #000;
}

.ravenous .image-container img {
  width: 100%;
  height: 100%;
}

/* App Styles */

.ravenous {
  font-family: 'Work Sans', sans-serif;
}

.ravenous h1 {
  padding: .66rem 0;
  text-align: center;
  background-color: #6d582e;
  font-family: Poppins, sans-serif;
  font-size: 1.8rem;
  color: #fff;
  margin: 0;
}